.IntroText{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #f2e5dd;
    /* overflow: hidden; */
        overflow-y: auto;
    overflow-x: hidden;
}

.intro-continer{
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    margin-top: 18vh;
    width: 94vw;
    height: 169vh;
    background-color: #e3d4c8;
    border-radius: 107px 107px 0 0;
}

.title-div{
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    margin-top: -23vh;
    width: 94vw;
    height: 21vh;
    background-color: #d6c5b6;
    border-radius: 107px 107px 0 0;
}

.title-intro{
    position: absolute;
    right: 50%;
    transform: translateX(50%);
    font-family: 'Karantina';
    font-size: 10.8vw;
    color: #175a85;
    margin-top: 6vh;
    width: 99%;
    text-align: center;
    z-index: 2;
}

.text-intro-div{
    position: absolute;
    right: 50%;
    transform: translateX(50%);
    margin-top: 23vh;
    width: 80vw;
    /* overflow-y: auto; */
    /* overflow-x: hidden; */
    text-align: center;
    height: 55vh;
}


.text-intro{
    font-family: 'Heebo', sans-serif;
    font-size: 5vw;
    color: black;
}

.about-div{
    position: absolute;
    right: 50%;
    transform: translateX(50%);
}

.homePageLogos {
    position: absolute;
    transform: translateX(50%);
    right: 50%;
    height: auto;
    width: 33vw;
    margin-top: 25vh;
}


.subtitle {
    font-family: 'Heebo', sans-serif;
    font-size: 4vw;
    color: grey;
    text-align: center;
    position: absolute;
    margin-top: 4vh;
    right: 50%;
    transform: translateX(50%);
    width: 63vw;
    direction: rtl;
}

.date {
    position: absolute;
    right: 50%;
    transform: translateX(50%);
    font-size: 2vh;
    width: 20vw;
    color: #50565c;
    font-weight: bolder;
    margin-top: 17vh;
    font-family: 'Heebo', sans-serif;
}


.mashov-home {
    position: absolute;
    right: 50%;
    transform: translateX(50%);
    font-family: 'Heebo', sans-serif;
    font-size: 4vw;
