    text-align: center;
    margin-top: 36vh;
    width: 67vw;
}

.mashovText {
    margin-bottom: 4vh;
}

#linkHome {
    color: #50565c;
}

.btn-start{
    direction: rtl;
    font-family: 'Karantina', cursive;
    color: white;
    position: absolute;
    right: 50%;
    transform: translateX(50%);
    margin-top: 147vh;
    width: 59vw;
    height: 6vh;
    padding: 2vh;
    text-align: center;
    font-size: 11vmin;
    /* font-weight: bold; */
