.intro-Simulation{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    height: 100vh; /* Ensure the entire screen is used */
    background-color: #f2e5dd;
    overflow-y: auto;
    overflow-x:hidden;
}

.touch-logo{
    width: 13vw;
    top: 23vh;
    position: relative;
    z-index: 2;
}

.title1 {
    font-family: 'Karantina';
    font-size: 11.8vw;
    color: #175a85;
    margin-top: 4vh;
    text-align: center;
    width: 60vw;
    top: 19vh;
    position: relative;
    z-index: 2;
}

.into-text-Simulation{
    font-family: 'Heebo', sans-serif;
    font-size: 4.7vw;
    color: grey;
    text-align: center;
    width: 86vw;
    padding: 5vw;
    position: relative;
    margin-top: 2vh;
    min-height: 0;
    direction: rtl;
    padding-bottom: 13vh;
}



.form-Simulation {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 80vw;
    margin-top: 6vh; /* Space below the intro text */
    gap: 2vh;
}

.form-row {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}

.form-label {
    font-family: 'Heebo', sans-serif;
    font-size: 4vw;
    color: #333;
    width: 47%;
    text-align: right;
}

.form-input, .form-select {
    font-family: 'Heebo', sans-serif;
    color: #333;
    width: 42%;
    padding: 1vh;
    font-size: 3.8vw;
    border: 1px solid #ccc;
    border-radius: 5px;
    text-align: right;
}

.error-message {
    color: #e8535f;
    font-size: 3.5vw;
    margin-top: 10px;
    text-align: center;
    /* position: relative;
    right: 50%;
    transform: translateX(50%); */
}

.btn-start-Simulation{
    direction: rtl;
    font-family: 'Karantina', cursive;
    color: white;
    position: absolute;
    right: 50%;
    transform: translateX(50%);
    width: 40vw;
    height: 5vh;
    padding: 2vh;
    margin-top: 25vh;
    text-align: center;
    font-size: 9vmin;
    border-radius: 7vw;
    background-color: #21b3e1;
}
